"use client";

import { useState, useEffect, useRef } from "react";
import { useForm, SubmitHandler } from "react-hook-form";
import { stepsData } from "@/data";
import MyCart from "./MyCart";
import CartContact from "./CartContact";
import CartDelivery from "./CartDelivery";
import CartOrder from "./CartOrder";
import { Container } from "../container";
import { Stepper } from "../stepper";
import useStore from "@/context/store";
import { showError } from "../toast/Toast";
import { PageLoader } from "../loader";

// Form ma'lumotlari interfeysi, kerakli maydonlarni qo'shing
interface FormData {
  // masalan:
  // cartItems: string;
  // contactName: string;
  // deliveryAddress: string;
  // orderNotes: string;
  [key: string]: any;
}

const CartStepper = () => {
  const { auth, clearDataAfterTimeout } = useStore();
  const [activeStep, setActiveStep] = useState(0);
  const [isClient, setIsClient] = useState(false);
  const authErrorShown = useRef(false);

  useEffect(() => {
    if (!auth) {
      clearDataAfterTimeout();
    }
  }, [auth, clearDataAfterTimeout]);

  useEffect(() => {
    setIsClient(true);
  }, []);

  useEffect(() => {
    const timer = setTimeout(() => {
      if (auth === false && !authErrorShown.current) {
        showError("Вы не авторизованы, рекомендуем авторизоваться");
        authErrorShown.current = true;
      }
    }, 500);
    return () => clearTimeout(timer);
  }, [auth]);

  const steps = stepsData || [];
  const isLastStep = activeStep === steps.length - 1;

  const {
    register,
    handleSubmit,
    control,
    watch,
    formState: { errors },
  } = useForm<FormData>();

  const onSubmit: SubmitHandler<FormData> = (data) => {
    console.log("Form Data:", data);
  };

  if (!isClient) {
    return <PageLoader />;
  }

  if (auth === undefined) return null;

  const handleNextStep = () => {
    if (!isLastStep) {
      setActiveStep((prevStep) => prevStep + 1);
    }
  };

  const handlePrevStep = () => {
    if (activeStep) {
      setActiveStep((prevStep) => prevStep - 1);
    }
  };

  // Umumiy form metodlari va xatoliklarni har bir komponentga uzatamiz
  const commonFormProps = { register, control, errors, watch };

  const renderCurrentStepContent = () => {
    switch (activeStep) {
      case 0:
        return (
          <MyCart
            step={activeStep}
            onNextStep={handleNextStep}
            {...commonFormProps}
          />
        );
      case 1:
        return (
          <CartContact
            step={activeStep}
            onPrevStep={handlePrevStep}
            onNextStep={handleNextStep}
            {...commonFormProps}
          />
        );
      case 2:
        return (
          <CartDelivery
            step={activeStep}
            onPrevStep={handlePrevStep}
            onNextStep={handleNextStep}
            {...commonFormProps}
          />
        );
      case 3:
        return (
          <CartOrder
            step={activeStep}
            onPrevStep={handlePrevStep}
            {...commonFormProps}
          />
        );
      default:
        return null;
    }
  };

  return (
    <Container className="pt-5 pb-[58px]">
      <div className="hidden lg:block">
        <Stepper steps={steps} activeStep={activeStep} />
      </div>
      <form onSubmit={handleSubmit(onSubmit)} className="mt-[23px]">
        {renderCurrentStepContent()}
        {isLastStep && (
          <button type="submit" className="btn btn-primary mt-4">
            Submit
          </button>
        )}
      </form>
    </Container>
  );
};

export default CartStepper;
